// Woocommerce stuff

.woocommerce {
	ul.products {
		@extend .row;
		li.product {
			margin: 0;
			margin-bottom: $grid-gutter-width;
			@extend .col-6;
			@extend .col-md-4;
			@extend .col-lg-3;
			.woocommerce-loop-product__title {
				padding-top: 0;
				color: $primary;
				text-align: center;
			}
		}
	}
	div.product {
		div.images {
			width: 33%;
			.flex-control-thumbs {
				li {
					width: 18%;
					margin-left: 0;
					margin-top: 2%;
					margin-right: 2.5%;
					&:nth-child(4n+1) {
						clear: none;
					}
					&:nth-child(5n) {
						margin-right: 0;
					}
					&:nth-child(5n + 1) {
						clear: left;
					}
				}
			}
		}
		div.summary {
			width: 63%;
		}
		h1.product_title {
			@include font-size( $h3-font-size );
		}
	}
}

#woo-product-contact {
	margin-top: $grid-gutter-width;
	margin-bottom: $grid-gutter-width;
	text-align: center;
	width: 100%;
	aside {
		margin: $grid-gutter-width/2 0;
	}
	.btn {
		display: block;
		padding: .75rem 1.25rem;
	}
	a:not(.btn) {
		color: $gray;
		font-size: $h5-font-size;
	}
}

@include media-breakpoint-up(lg) {
	.woocommerce {
		div.product {
			div.images {
				width: 43% !important;
			}
			div.summary {
				width: 53% !important;
			}
		}
	}
	#woo-product-contact {
		width: 60%;
	}
}