// _navbar

.navbar-brand-wrapper {
	
}

.navbar-menu-wrapper {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	.menu-featured-container {
		display: none;
		@include media-breakpoint-up(xl) {
			display: block;
		}
	}
}

.navbar {
	padding: $grid-gutter-width/1.5 0;
}

.navbar-brand {
	margin: 0;
	padding: 0;
}

#hamburger-icon {
	position: relative;
	padding: 0;
	width: 40px;
	height: 30px;
	margin-top: -4px;
	display: block;
	border: none;
	text-align: center;
	.more,
	.menu {
		position: absolute;
		left: 0;
		top: 0;
		width: 40px;
		font-size: 12px;
		font-weight: 500;
		color: $secondary;
		transition: transform $time ease;
	}
	.menu { display: block; }
	.more { display: none; }
	@include media-breakpoint-up(lg) {
		.menu { display: none; }
		.more { display: block; }
	}
	.line {
		display: block;
		position: absolute;
		left: 0;
		width: 40px;
		transition: transform $time ease;
		background: $primary;
		--s: 4px; /* size of the wave */
		--b: 2px; /* thickness of the line */
		--m: 0.8; /* curvature of the wave [0 2] */

		--R: calc(var(--s)*sqrt(var(--m)*var(--m) + 1) + var(--b)/2);
		height: calc(2*var(--R));
		width: 100%;
		--_g: #0000 calc(99% - var(--b)), #000 calc(101% - var(--b)) 99%, #0000 101%;
		mask:
		radial-gradient(var(--R) at left 50% bottom calc(-1*var(--m)*var(--s)), var(--_g)) 
		calc(50% - 2*var(--s)) calc(50% - var(--s)/2 - var(--b)/2)/calc(4*var(--s)) calc(var(--s) + var(--b)) repeat-x,
		radial-gradient(var(--R) at left 50% top calc(-1*var(--m)*var(--s)),var(--_g)) 
		50% calc(50% + var(--s)/2 + var(--b)/2)/calc(4*var(--s)) calc(var(--s) + var(--b)) repeat-x;
		&.line-1 {
			top: 50%;
			top: calc( 50% - 4px);
		}
		&.line-2 {
			top: 100%;
			top: calc( 100% - 12px);

		}
	}
	&:hover, &:active, &:focus {
		.more {
			transform: translateY(2px * -1);
		}
		.line-2 {
		    transform: translateY(2px);
		}  
	}
}

#main-menu {
	font-family: $font-family-sans-serif;
	font-weight: 500;
	text-transform: uppercase;
	font-size: 0.9rem;
	padding-right: $grid-gutter-width/1.5;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	@include media-breakpoint-up(xl) {
		font-size: 1rem;
	}
	li {
		text-align: center;
	}
	a {
		display: inline-block;
		color: $primary;
		transition: all $time ease;
		text-align: center;
		text-decoration: none;
		padding: 5px;
		margin: 0 2px;
		@media (min-width: 1300px) {
			padding: 10px;
			margin: 0 5px;
		}
		&:hover,
		&:active {
			color: lighten( $primary, 10% );
		}
		&.nav-link {
			@include media-breakpoint-up(lg) {
				border-top: 3px solid rgba(255,255,255,0);
				border-bottom: 3px solid rgba(255,255,255,0);
				&:hover,
				&:active {
					border-bottom: 3px solid $primary;
				}
			}
		}
	}
}

.offcanvas-collapse {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 100%;
  width: 500px;
  max-width: 80%;
  padding: $grid-gutter-width;
  overflow-y: auto;
  visibility: hidden;
  z-index: 2002;
  background-color: $primary;
  transition: visibility $time ease, -webkit-transform $time ease;
  transition: transform $time ease, visibility $time ease;
  transition: transform $time ease, visibility $time ease, -webkit-transform $time ease;
  color: #FFFFFF;
  a, h1, h2, h3, h4, h5, h6 {
  	color: #FFFFFF;
  }
  .offcanvas-title {
  	color: $secondary;
  }
  .btn-close {
  	position: absolute;
  	line-height: 1;
  	top: $grid-gutter-width;
  	right: $grid-gutter-width;
  	color: #FFFFFF;
  }
  ul.accordion {
  	list-style: none;
  	padding: 0;
  	a {
  		font-family: $font-family-sans-serif;
  		font-size: 1.1rem;
  		font-weight: 500;
  		text-decoration: none;
	  	padding-left: 0;
	  	padding-right: 0;
	  	display: flex;
	  	justify-content: space-between;
	  	align-items: center;
	  	&.collapsed {
	  		i {
	  			transform: rotate(-90deg);
	  		}
	  	}
	  	i {
	  		transform: rotate(0deg);
	  	}
	  }
	  ul {
			list-style: none;
			padding-left: $grid-gutter-width;
	  }
  }
}

.offcanvas-collapse.open {
  visibility: visible;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}

body {
	.offcanvas-bg {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: transparent;
		z-index: -101;
		opacity: 0;
		display: none;
		transition: opacity $time ease;
	}
	&.menuOpen {
		.offcanvas-bg {
			display: block;
			opacity: 0.75;
			background: #000;
			z-index: 2001;
		}
	}
}
