// _submenu

#submenu-wrapper {
	width: 100%;
	max-width: ($size-xl - $grid-gutter-width);
    margin-right: auto;
    margin-left: auto;
	a {
		display: block;
		width: 100%;
		color: #FFF;
		padding: 30px 10px;
		text-align: center;
		text-transform: uppercase;
		&:hover,
		&:active {
			background-color: #722D1F;
		}
		&.current {
			background-color: #722D1F;
		}
		i {
			font-size: 1.8rem;
		}
	}
}

#submenu {
	background-color: $primary;
	border-top: 1px solid #FFF;
	min-height: $grid-gutter-width;
}
