// _homepage

#about-wrapper {
  background-color: $primary;
  position: relative;
  p, h1, h2, h3, h4, h5 {
    color: #FFF;
  }
  a.btn {
    color: #FFF;
    border-color: #FFF;
    &:hover,
    &:active {
      color: $primary;
      background-color: #FFF;
    }
  }
}

#events-wrapper {
  margin-bottom: $grid-gutter-width*2;
}

.grid-gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-gap: 15px;
  margin-bottom: 30px;
  z-index: 101;
  @include media-breakpoint-up(md) {
    position: absolute;
    top: -100px;
    right: 0;
    bottom: -100px;
    left: 0;
    margin-bottom: 0;
  }
  &.grid-one {
    .gallery-item-0 {
      grid-column-start: 1;
      grid-column-end: 5;
      grid-row-start: 1;
      grid-row-end: 5;
    }
  }
  &.grid-three {
    .gallery-item-0 {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 5;
    }
    .gallery-item-1 {
      grid-column-start: 3;
      grid-column-end: 5;
      grid-row-start: 1;
      grid-row-end: 3;
    }
    .gallery-item-2 {
      grid-column-start: 3;
      grid-column-end: 5;
      grid-row-start: 3;
      grid-row-end: 5;
    }
  }
  .gallery-item {
    display: flex;
    align-items: center;
    .gallery-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: $radius;
    }
  }
}

#banner-cta {
  p,h1,h2,h3,h4,h5,h6 {
    color: #FFF;
  }
}

#cta-wrapper {
  position: relative;
  padding-top: $grid-gutter-width*4;
  padding-bottom: $grid-gutter-width*4;
  color: #FFF;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgba(0,0,0,0.55);
    z-index: 1;
  }
  h1, h2, h3 {
    color: #FFF;
  }
  a.btn {
    color: #FFF;
    border-color: #FFF;
    &:hover,
    &:active {
      color: $primary;
      background-color: #FFF;
    }
  }
}