// _Hero

#hero {
	position: relative;
	width: 100%;
	overflow: hidden;
	&.noImage {
		background-color: $lighter;
		.hero-content {
			position: relative;
			h1, h2, h3, p {
				color: $black !important;
			}
		}
	}
	&.hasImage {
		&:before {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			width: 100%;
			background: rgba(0,0,0,0.5);
			background: linear-gradient(90deg, rgba(0,0,0,0.85) 0%, rgba(0,0,0,0.5) 50%, rgba(0,0,0,0.4) 100%);
			z-index: 1001;
			@include media-breakpoint-up(lg) {
				background: linear-gradient(90deg, rgba(0,0,0,0.85) 0%, rgba(0,0,0,0.5) 50%, rgba(0,0,0,0.4) 100%);
			}
		}
		.hero-content {
			padding: $grid-gutter-width 0;
		}
		.home-image {
			.hero-content {
				@include media-breakpoint-up(md) {
					padding: 100px 0;
				}
				@include media-breakpoint-up(lg) {
					padding: 180px 0;
				}
				@include media-breakpoint-up(xl) {
					padding: 220px 0;
				}
			}
		}
		.page-image {
			.hero-content {
				@include media-breakpoint-up(md) {
					padding: 60px 0;
				}
				@include media-breakpoint-up(lg) {
					padding: 80px 0;
				}
				@include media-breakpoint-up(xl) {
					padding: 100px 0;
				}
			}
		}
	}
	.hero-content {
		z-index: 1003;
		overflow: hidden;
		width: 100%;
		.hero-content-inner {
			position: relative;
			padding: $grid-gutter-width*1.5 0;
			h1 {
				color: #FFF;
				font-size: 34px;
				line-height: 1.25;
				font-style: normal;
				text-decoration: none;
				letter-spacing: 1px;
				text-transform: none;
				font-weight: 700;
				margin: 10px 0;
				@include media-breakpoint-up(md) {
					font-size: 40px;
				}
				@include media-breakpoint-up(lg) {
					font-size: 50px;
				}
			}
			#big-text {
				font-size: 1.75em;
				color: $secondary;
			}
			a.btn {
				margin-top: 10px;
			}
			p {
				color: #FFF;
				margin: 10px 0;
				@include media-breakpoint-up(md) {
					margin: 15px 0;
					font-size: 1.2rem;
				}
				@include media-breakpoint-up(lg) {
					margin: 15px 0;
					font-size: 1.3rem;
				}
			}
		}
	}
}

.home {
	#hero {
		.hero-content {
			.hero-content-inner {
				@include media-breakpoint-up(md) {
					max-width: 60%;
				}
				@include media-breakpoint-up(lg) {
					max-width: 50%;
				}
				@include media-breakpoint-up(xl) {
					max-width: 45%;
				}
			}
		}
	}
}

.flag-content {
	overflow: visible;
	@include media-breakpoint-up(lg) {
		&:before {
			content: "";
			position: absolute;
			display: block;
			top: 0;
			bottom: 0;
			right: calc( 100% + 50px );
			width: 1000px;
			background-color: $secondary;
		}
	}
	.flag {
		position: absolute;
		top: 0;
		bottom: 0;
		left: -50px;
		width: 50px;
		background-color: $secondary;
		clip-path: polygon(0 0, 85% 0, 50% 50%, 85% 100%, 0 100%);
		@include media-breakpoint-up(lg) {
			left: -60px;
			clip-path: polygon(0% 0%,100% 0,40% 50%,100% 100%,0 100%);
		}
	}
}
