// _font

@import url("https://use.typekit.net/snn4xdi.css");

//typography
$font-family-sans-serif:      "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-family-serif:           "meno-display","Times New Roman", Times, "Didot", serif;
$font-family-monospace:       SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-base:            $font-family-sans-serif !default;

$font-size-base:              1rem;
$font-size-small:             $font-size-base * 0.95;
$font-size-tiny:              $font-size-base * 0.9;

$h1-font-size:                $font-size-base * 3.5;
$h2-font-size:                $font-size-base * 3;
$h3-font-size:                $font-size-base * 2.4;
$h4-font-size:                $font-size-base * 1.8;
$h5-font-size:                $font-size-base * 1.2;
$h6-font-size:                $font-size-base * 1;

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  font-family: $font-family-serif;
  font-weight: 600;
  margin-bottom: 1rem;
  line-height: 1.3;
  letter-spacing: 1px;
  color: $primary;
}

.h1, h1 {
  @include font-size( $h1-font-size );
}

.h2, h2 {
  @include font-size( $h2-font-size );
}

.h3, h3 {
  @include font-size( $h3-font-size );
}

.h4, h4 {
  @include font-size( $h4-font-size );
}

.h5, h5 {
  @include font-size( $h5-font-size );
}

.h6, h6 {
  @include font-size( $h6-font-size );
}
