// This is an empty scss file for your custom styles - Please add your super duper awesome mega design styles here

html, body {
  font-size: $font-size-base;
  height: 100%;
}

body {
	font-family: $font-family-sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	color: $black;
	@include media-breakpoint-up(lg) {
		font-size: $font-size-base * 1.1;
	}
}

.btn {
	font-family: $font-family-serif;
	border-radius: $radius;
	font-size: 1rem;
	font-weight: 600;
	line-height: 1.1;
	padding: 1rem 1rem 0.95rem 1rem;
	text-transform: uppercase;
	text-decoration: none;
	transition: all $time ease;
	&.btn-lighter {
		font-weight: 500;
		color: $primary;
		border: 1px solid #FFF;
		background-color: rgba(255,255,255,0.6);
		&:hover,
		&:active {
			background-color: #FFF;
		}
	}
	&.btn-outline {
		font-weight: 600;
		color: $primary;
		border: 1px solid $primary;
		background-color: transparent;
		&:hover,
		&:active,
		&.active {
			color: #FFF;
			background-color: $primary;
		}
	}
	&.btn-secondary {
		color: #FFF;
		&:hover,
		&:active {
			border-color: #b89727;
			background-color: #b89727;
		}
	}
	&.aligncenter {
		display: inline-block !important;
		margin-left: auto;
		margin-right: auto;
	}
}
.wp-block-button__link {
	font-family: $font-family-serif;
	border-radius: $radius;
	font-size: 1rem;
	font-weight: 600;
	line-height: 1.1;
	padding: 1rem 1rem 0.95rem 1rem;
	text-transform: uppercase;
	text-decoration: none;
	background-color: $primary;
	color: #FFF;
	transition: all $time ease;
	&:hover,
	&:active {
		color: #FFF;
		border-color: darken( $primary, 10% );
		background-color: darken( $primary, 10% );
	}
}
a[href^="https://fareharbor.com"] {
	font-family: $font-family-serif;
	border-radius: $radius;
	font-size: 1rem;
	font-weight: 600;
	line-height: 1.1;
	text-align: center;
	padding: 1rem 1rem 0.95rem 1rem;
	text-transform: uppercase;
	text-decoration: none;
	transition: all $time ease;
	color: $primary;
	border: 1px solid $primary;
	background-color: transparent;
	&:hover,
	&:active,
	&.active {
		color: #FFF;
		background-color: $primary;
	}
}
#secondary a[href^="https://fareharbor.com"] {
	display: block;
	color: #FFF;
	border-color: $secondary;
	background-color: $secondary;
	&:hover,
	&:active,
	&.active {
		border-color: darken( $secondary, 5% );
		background-color: darken( $secondary, 5% );
	}
}

.btn-close {
	border: none;
	background: transparent;
}

hr {
	color: $lighter;
}

figure {
	margin: 0;
}

.bordered {
	&.bordered-light {
		border: 1px solid $lighter;
	}
}

.para-container {
	position: relative;
	overflow: hidden;
	background-color: $black;
	.para-img {
		position: absolute;
	  bottom: 0;
	  left: 0;
	  width: 100%;
	  height: 130%;
	  object-fit: cover;
	  object-position: center center;
	  padding: 0;
	  z-index: 0;
	  opacity: 0;
	  transition: opacity $time ease;
	  transition-delay: $time/2;
	}
	.para-content {
		position: relative;
		z-index: 1;
	}
}

.card {
	border-radius: $radius;
	overflow: hidden;
	background-color: $lighter;
	border: none;
	&.offset {
		background-color: #FFF;
	}
	.card-img-top {
		position: relative;
    overflow: hidden;
    height: 0;
    padding-top: 66.667%;
    img, iframe {
    	width: 100%;
    	height: 100%;
			object-fit: cover;
			object-position: center top;
			border-top-left-radius: $radius;
			border-top-right-radius: $radius;
			position: absolute;
			top: 0;
    }
	}
	.card-top {
    min-height: 115px;
    padding: 1.25rem;
    background-color: $primary;
    display: flex;
		justify-content: center;
		align-items: center;
    h1, h2, h3, h4, h5, h6 {
    	color: #FFF;
    	margin: 0;
    }
  }
	.card-body {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		&.offset {
			position: relative;
			padding-right: 10px;
			margin-top: -$grid-gutter-width*2;
    	margin-left: $grid-gutter-width;
    	margin-bottom: $grid-gutter-width;
    	background-color: #fff;
		}
		.card-text {
			min-height: 40px;
			margin-bottom: $grid-gutter-width/2;
			&.has-icon {
				position: relative;
				padding-left: $grid-gutter-width*1.5;
				.icon {
					position: absolute;
					top: 0;
					left: 0;
					font-size: 1.5rem;
					width: $grid-gutter-width;
					text-align: center;
					i {
						font-size: 1.5rem;
					}
				}
			}
			p {
				margin-bottom: 0;
			}
		}
		.btn {
			align-self: flex-start;
		}
	}
}

.site {
	position: relative;
	overflow: hidden;
	width: 100%;
	max-width: $size-xxl;
	margin: 0 auto;
}

.container-fluid {
	max-width: ($size-xl - $grid-gutter-width) !important;
	padding-left: $grid-gutter-width;
	padding-right: $grid-gutter-width;
	@include media-breakpoint-up(lg) {
		padding-left: $grid-gutter-width*2;
		padding-right: $grid-gutter-width*2;
	}
	@include media-breakpoint-up(xl) {
		padding-left: $grid-gutter-width*4;
		padding-right: $grid-gutter-width*4;
	}
}

.padder {
	padding-top: $grid-gutter-width;
	padding-bottom: $grid-gutter-width;
	@include media-breakpoint-up(md) {
		padding-top: $grid-gutter-width*2;
		padding-bottom: $grid-gutter-width*2;
	}
	&.top {
		padding-bottom: 0;
	}
	&.bottom {
		padding-top: 0;
	}
}

.padder-sm {
	padding-top: $grid-gutter-width;
	padding-bottom: $grid-gutter-width;
	&.top {
		padding-bottom: 0;
	}
	&.bottom {
		padding-top: 0;
	}
}

.padder-lg {
	padding-top: $grid-gutter-width*2;
	padding-bottom: $grid-gutter-width*2;
	@include media-breakpoint-up(lg) {
		padding-top: $grid-gutter-width*4;
		padding-bottom: $grid-gutter-width*4;
	}
	&.top {
		padding-bottom: 0;
	}
	&.bottom {
		padding-top: 0;
	}
}

.padder-both {
	padding-top: $grid-gutter-width !important;
	padding-bottom: $grid-gutter-width !important;
	@include media-breakpoint-up(md) {
		padding-top: $grid-gutter-width*2 !important;
		padding-bottom: $grid-gutter-width*2 !important;
	}
}

.wrapper {
	margin-top: $grid-gutter-width;
	margin-bottom: $grid-gutter-width;
	@include media-breakpoint-up(md) {
		margin-top: $grid-gutter-width*2;
		margin-bottom: $grid-gutter-width*2;
	}
	@include media-breakpoint-up(lg) {
		margin-top: $grid-gutter-width*3;
		margin-bottom: $grid-gutter-width*3;
	}
	&.top {
		margin-bottom: 0;
	}
	&.bottom {
		margin-top: 0;
	}
}

.wrapper-sm {
	margin-top: $grid-gutter-width;
	margin-bottom: $grid-gutter-width;
	&.top {
		margin-bottom: 0;
	}
	&.bottom {
		margin-top: 0;
	}
}

.wrapper-lg {
	margin-top: $grid-gutter-width*3;
	margin-bottom: $grid-gutter-width*3;
	@include media-breakpoint-up(lg) {
		margin-top: $grid-gutter-width*4;
		margin-bottom: $grid-gutter-width*4;
	}
}

.wrapper-side {
	padding-left: $grid-gutter-width/2;
	padding-right: $grid-gutter-width/2;
	@include media-breakpoint-up(md) {
		padding-left: $grid-gutter-width*2;
		padding-right: $grid-gutter-width*2;
	}
	@include media-breakpoint-up(lg) {
		padding-left: $grid-gutter-width*4;
		padding-right: $grid-gutter-width*4;
	}
}

.wrapper-none {
	margin-top: 0 !important;
	margin-bottom: 0 !important;
}

.text-serif {
	font-family: $font-family-serif;
}

.text-lighter {
	color: $secondary !important;
}

.text-sm {
	font-size: $font-size-small;
}

.w-100 {
	width: 100%;
}

.bg-primary {
	background-color: $primary;
}

.bg-secondary {
	background-color: $secondary;
}

.bg-lighter {
	background-color: $lighter;
}

.pb-lab {
	padding-bottom: $grid-gutter-width*6;
	@include media-breakpoint-up(lg) {
		padding-bottom: $grid-gutter-width*4;
	}
}

.mt-lab {
	margin-top: $grid-gutter-width*2;
	@include media-breakpoint-up(lg) {
		margin-top: $grid-gutter-width*2;
	}
}

.mb-lab {
	margin-bottom: $grid-gutter-width*2;
	@include media-breakpoint-up(lg) {
		margin-bottom: $grid-gutter-width*2;
	}
}

#secondary {
	.card {
		//border-top: 2px solid $primary;
	}
}

#banner-title {
	position: relative;
	padding-left: $grid-gutter-width;
	@include media-breakpoint-up(lg) {
		padding-left: 0;
	}
	&:before {
		content: "";
		position: absolute;
		z-index: 0;
		top: 0;
		bottom: 0;
		left: 0;
		right: 50%;
		background-color: $primary;
	}
	&:after {
		content: "";
		position: absolute;
		z-index: 1;
		top: 0;
		bottom: 0;
		left: 0;
		width: $grid-gutter-width;
		background-color: $secondary;
	}
	p,a,h1,h2,h3,h4,h5,h6 {
		color: #FFF;
	}
}

img.alignleft,
figure.alignleft,
.wp-block-image {
	margin: 0;
	figure.alignleft {
		float: none;
		margin: $grid-gutter-width auto $grid-gutter-width auto !important;
		@include media-breakpoint-up(lg) {
			float: left;
			margin: 0 $grid-gutter-width*1.5 $grid-gutter-width*1.5 0 !important;
		}
	}
	figure.alignright {
		display: table;
		float: none;
		margin: $grid-gutter-width auto $grid-gutter-width auto !important;
		@include media-breakpoint-up(lg) {
			float: right;
			margin: 0 0 $grid-gutter-width*1.5 $grid-gutter-width*1.5 !important;
		}
	}
	figure.aligncenter {
		text-align: center;
	}
}

figure.size-large {
	@include media-breakpoint-up(lg) {
		max-width: 50%;
	}
}

a {
	transition: all $time ease;
	text-decoration: underline;
	color: $primary;
	cursor: pointer;
	&:hover,
	&:active {
		text-decoration: none;
		color: lighteren( $primary, 10% );
	}
	label {
		cursor: pointer;
	}
	h1, h2, h3, h4, h5 {
		color: $primary !important;
		&:active {
			text-decoration: none;
			color: lighteren( $primary, 10% );
		}
	}
}

figure {
	border-radius: $radius;
	.wp-caption-text {
		font-size: 0.9em;
		text-align: center;
		font-style: italic;
	}
}

figure.sixteen-nine-img {
	width: 100%;
	overflow: hidden;
	padding-top: 56.25%;
	position: relative;
	img, iframe {
		width: 100%;
    height: 100%;
    object-fit: contain;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

figure.four-six-img {
	width: 100%;
	overflow: hidden;
	margin-bottom: $grid-gutter-width/2;
	padding-top: 66.667%; /* 66.667% = 100 * (6 / 4) */
	position: relative;
	img, iframe {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, -50%);
	}
}

figure.cover img,
img.cover {
	object-fit: cover;
}
figure.contain img,
img.contain {
	object-fit: contain;
}

.embed-container { 
	position: relative; 
	padding-bottom: 56.25%;
	overflow: hidden;
	max-width: 100%;
	height: auto;
}
.embed-container iframe,
.embed-container object,
.embed-container embed { 
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.wp-block-columns {
	margin-bottom: 0;
	.wp-block-column {
    	@include media-breakpoint-down(sm) {
    		margin: 0;
    		margin-bottom: $grid-gutter-width;
    		flex-basis: 100% !important;
    		flex-grow: 1;
    	}
    	.wp-block-column {
    		margin: 0;
    	}
    }
}

.pagination {
	justify-content: center;
}

ul.list-style-check {
	list-style: none;
	margin-bottom: 0;
	li {
		position: relative;
		&:before {
		  content: '';
		  color: $secondary;
		  position: absolute;
		  left: 0;
		  top: 0;
		}
	}
}

.freyed {
	padding-top: $grid-gutter-width*3;
	padding-bottom: $grid-gutter-width*3;
	&:before,
	&:after {
		content: "";
		display: block;
		position: absolute;
	}
}
.freyed:before {
	top: 0;
	left: 0;
	width: 100%;
	height: 40px;
  background: url(../img/edge-top.png) repeat-x top left;
  z-index: 0;
}
.freyed:after {
	bottom: 0;
	left: 0;
	width: 100%;
	height: 40px;
  background: url(../img/edge-bot.png) repeat-x bottom left;
  z-index: 0;
}

#hours {
	text-align: center;
	.location {
		margin-bottom: $grid-gutter-width;
		background-color: $lighter;
		border: none;
		margin-bottom: $grid-gutter-width;
		transition: opacity $time ease;
		&.location-admission {
			background-color: $secondary;
			.card-body {
				text-align: center;
				display: flex;
				justify-content: center;
				p, h1, h2, h3, h4, h5, h6 {
					color: $primary-dark;
				}
			}
			ul {
				padding: 0;
				list-style: none;
				text-align: center;
				li {
					margin-bottom: 10px;
				}
			}
			.btn {
				align-self: center;
				margin-bottom: 10px;
			}
		}
		.card-body {
			text-align: left;
		}
	}
}

#directions-wrapper {
	.dir-figure {
		min-height: 300px;
		margin-bottom: $grid-gutter-width;
		@include media-breakpoint-up(md) {
			position: absolute;
			top: 0;
			bottom: 0;
			right: $grid-gutter-width/2;
			left: $grid-gutter-width/2;
			margin-bottom: 0;
		}
		.dir-map {
			width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: $radius;
		}
	}
}

#benefits-wrapper {
	
	.container-fluid {
		position: relative;
	}
	.image-right {
		position: absolute;
		display: none;
		top: 50%;
		right: 30px;
		transform: translateY(-50%);
		@include media-breakpoint-up(xl) {
			display: block;
			max-width: 500px;
			right: 15px;
		}
	}
}

.level {
	margin-bottom: $grid-gutter-width;
}

